@import 'assets/scss/utils/mixins';

a {
	text-decoration: none;
}

.mat-icon {
	height: 40px !important;
	width: 40px !important;
	display: inline-block !important;
	vertical-align: middle;
}

.social-icons {
	font-size: 14px;
	line-height: 18px;
	font-weight: 400;
	color: var(--general-3);
	display: block;
}

.img-fluid {
	width: 100px;
}

.email-icon,
.download-app,
.icon-size {
	font-size: 20px;
	height: 20px !important;
	width: 20px !important;
}

.mat-divider {
	border-top-color: var(--neutral-2) !important;
}
